

.nav {
    background: #1d7baa;
    border-radius: 10px;
    margin-left:100px;
    margin-right: 100px;
    height: 50px;
    display:flex;
    align-items: center;
}
@media only screen and (max-width: 1080px) {
    .nav {
        height: 40px;
        margin-left: 10px;
        margin-right: 10px;
        justify-content: center;
    }
}
#home {
    margin-left: 1%;
    color: white;
    font-family: 'Courier New', Courier, monospace;
    font-size: 26px;
}
@media only screen and (max-width: 1080px) {
    #home {
        font-size: 26px;
    }
}
#addNoteContainer{
    margin:2%;
    /* background: red; */
}
#addNoteForm {
    /* background: #b3d5e6; */
    margin:0 auto;
    width:50%;
    text-align: center;
}
@media only screen and (max-width: 1080px) {
    #addNoteForm {
        width:80%;
    } 
}
.addNoteInput {
    border:none;
    width:98%;
    height: 20px;
    border-radius: 10px;
    padding: 2%;
    margin:1%;
    font-size: 20px;
}
@media only screen and (max-width: 1080px) {
    .addNoteInput {
        height: 40px;
        font-size: 24px;
    }
}
#addNoteButton {
    background: #e4f5fd;
    border-radius: 10px;
    border: none;
    padding: 2%;
    width: 50px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
}
@media only screen and (max-width: 1080px) {
    #addNoteButton {
        width: 90px;
        font-size: 26px;
    }
}
#allNotesContainer{
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    /* grid-template-columns: 50% 50%; */
}
@media only screen and (max-width: 1080px) {
    #allNotesContainer{
        display: grid;
        /* grid-template-columns: 25% 25% 25% 25%; */
        grid-template-columns: 50% 50%;
    }
}
#noteBox{
    background: #aaccdd;
    margin: 5%;
    border-radius: 5px;
    padding: 3%;
}
#noteTitle{
    font-size: 22px;
    font-family: Arial, Helvetica, sans-serif;
    /* background: #cccccc; */
    
    /* the following two are 
    needed to center the title*/
    display: block;
    text-align: center;
}
@media only screen and (max-width: 1080px) {
    #noteTitle {
        /* font-size: 24px; */

    }
}
#noteText{
    display:block;
}
@media only screen and (max-width: 1080px) {
    #noteText {
        font-size: 18px;

    }
}

